.contact-info {
  color: white;
  width: 90%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between; /*      */
  padding: 20px; /*      */
}
.contact-info h1 {
  font-size: 42px;
  margin: 0;
  background: linear-gradient(90deg, #3AA1FF, #6BD6FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.contact-info h2 {
  font-size: 32px;
  margin: 15px 0;
  color: #3AA1FF;
}

.contact-info h3 {
  font-size: 26px;
  margin: 10px 0;
  color: #6BD6FF;
}
.contacts-rusLink,
.contacts-questionnaire {
  display: flex;
  align-items: center;
  gap: 10px; /*      */
  cursor: pointer;
  transition: transform 0.3s, opacity 0.3s; /*   */
  color: #fff;
  opacity: 0.9; /*    */
  font-size: 24px;
}

.contacts-rusLink:hover,
.contacts-questionnaire:hover {
  transform: scale(1.05); /*     */
}

.contacts-rusLink svg,
.contacts-questionnaire svg {
  width: 20px; /*    */
  height: 20px;
}

.contact-info h1,
.contact-info h2 {
  margin-top: 20px; /*        */
  margin-bottom: 20px; /*    */
}

.color-blue {
  color: #3AA1FF;
}

.contacts-honorable-list,
.colleagues-contacts-honorable-list {
  display: flex;
  flex-direction: column; /*        */
  margin-left: 0; /*    */
}

.contacts-honorable-list h2,
.colleagues-contacts-honorable-list h2 {
  font-size: 36px; /*       */
}

.contacts-honorable-list h3,
.colleagues-contacts-honorable-list h3 {
  font-size: 28px; /*    */
}

.contacts-honorable-list li,
.colleagues-contacts-honorable-list li {
  font-size: 24px; /*      */
  margin-top: 5px;
}

.librarian-patron li {
  font-weight: bold; /*     */
}

.text-librarian-patron li {
  font-weight: normal; /*       */
}

.lost-book-and-sp {
  margin-top: 40px; /*    */
}



.contact-info button {
  height: 60px; /*        */
  width: 60px;
  transition: transform 0.3s, opacity 0.3s;
}

.contact-info button:hover {
  transform: scale(1.1); /*       */
}

.contact-tooltip {
  display: flex;
  position: absolute;
  text-align: center;
  width: fit-content;
  background-color: #333;
  color: #fff;
  font-size: 14.4px;
  padding: 4px;
  border-radius: 4px;
  z-index: 1000;
  top: -1.6px;
  transform: translate(0, -100%);
  font-weight: 500;
  opacity: 0;
}

.contact-tooltip--active {
  opacity: 1;
}

.contact-info button {
  height: 75px;
  width: 75px;
  opacity: 0.8;
  transition: 0.4s;
  position: relative;
  margin-right: 50px;
  opacity: 1;
}
.contact-info button:hover {
  opacity: 1;
  transform: scale(1.2);
}
.contact-info button svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
