:root {
    font-size: 16px; /*      rem */
}

.guide-info-container {
    width: 90%; /*   90% */
    padding: 0;
    margin: 0rem auto 0; /*   20px = 1.25rem   */
    box-sizing: border-box; /*        */
}

.guide-info-container .search {
    width: 100%;
    box-sizing: border-box;
}

.guide-info-container .search input {
    width: 80%;
}

.guide-info-container .search .btn-clear {
    position: absolute;
    right: 40px;
    top: 50%;
    transform: translate(0,-50%);
}

.guide-info-container .search .btn-search {
    position: absolute;
    top: 50%;
    transform: translate(0,-50%);
}

.guide-info-list {
    list-style-type: none;
    padding: 0;
    display: flex; /*  flexbox    */
    flex-wrap: wrap; /*         */
    gap: 0.9375rem; /*   : 15px = 0.9375rem */
    margin: 20; /* ,        */
}

.guide-info-item {
    background-color: #25272d; /*    */
    border-radius: 0.75rem; /*    */
    box-shadow: 0 0.25rem 0.9375rem rgba(0, 0, 0, 0.2); /*    */
    padding: 0.9375rem; /*   */
    max-width: 22.5rem; /*    */
    transition: transform 0.3s, box-shadow 0.3s;
    min-height: 1.5rem; /*   : 200px = 12.5rem */
    box-sizing: border-box; /*        */
}

.guide-info-link-wrapper {
    display: block;
    text-decoration: none;
    color: inherit;
}

.guide-info-content {
    position: relative; /*   */
}

.guide-info-date {
    font-size: 16px; /*     */
    color: #aaaaaa; /* -    */
    margin-bottom: 5px; /*       */
}

.guide-info-title {
    font-weight: bold;
    color: #ffffff; /*    */
    font-size: 32px; /*      */
    margin: 1px; /*   */
}

.guide-info-image-wrapper {
    aspect-ratio: 16/9; /*   */
    overflow: hidden;
    border-radius: 8px; /*    */    
}

.guide-info-image {
    width: 100%;
    transition: transform 0.3s ease-in-out;
    object-fit: cover; /*    ,  ,    */
}

.guide-info-image-wrapper:hover .guide-info-image {
    transform: scale(1.05); /*     */
}

.no-guides-message {
    color: #eaeaea;
    text-align: center;
    font-size: 18px;
}

.spacer {
    height: 20px; /*       */
}

.guide-tags {
    display: flex; /*  flexbox    */
    flex-wrap: wrap; /*       */
    gap: 0.5rem; /*    */
}

@media (max-width: 56.25rem) { /* 900px = 56.25rem */
    .guide-info-item {
        max-width: 21.5rem; /*    */
    }
}
@media (max-width: 50rem) { /* 800px = 50rem */
    .guide-info-item {
        max-width: 20.5rem; /*    */
    }
}
@media (max-width: 46.875rem) { /* 750px = 46.875rem */
    .guide-info-item {
        max-width: 17rem; /*    */
    }
}
@media (max-width: 38.875rem) { /* 622px = 38.875rem */
    .guide-info-item {
        max-width: 33.5rem; /*    */
    }
}
@media (max-width: 37.5rem) { /* 600px = 37.5rem */
    .guide-info-list{
        flex-direction: column;
    }
    .guide-info-item {
        max-width: 22.5rem; /*    */
    }
}