.search-container {
    display: flex;
    align-items: center;
    width: 100%; /*     100% */
}

.search-input {
    flex: 1; /*       */
    padding: 10px; /*  ,   */
    border: 1px solid #ccc; /*       */
    border-radius: 4px; /*   */
    margin-right: 10px; /*      */
}
.dropdown-tag-item {
    display: flex;
    background-color: #25272d; /*       */
    color: white;
    border-radius: 50px;
    border: 1px solid #101113; /*     */
    padding: 0.3rem 0.75rem; /*    */
    font-size: 1.25rem;
    cursor: pointer;
    transition: background-color 0.3s; /*     */
    text-align: center; /*  ,   */
    align-items: center; /*  ,   */

}
.tag-item {
    font-size: 1.25rem;
}
.tag-container {
    display: flex;
    flex-wrap: wrap; /*       */
    max-width: calc(100% - 5px); /*   */
}
.tags-container {
    display: flex; /*      */
    flex-wrap: wrap; /*       */
    max-width: 25rem;
    gap: 10px; /*    */
    padding-right: 0.5rem;
    padding-left: 0.1rem;
}
.tag-count-circle {
    background-color: #007bff;
    color: white;
    border-radius: 50%;
    padding: 4px;
    margin-left: 5px;
    font-size: 20px;
    line-height: 1;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 16px;
    height: 16px;
}
.btn-clear-all-tags {
    padding-left: 0.5rem;
    padding-bottom: 0.5rem;
    color:#007bff;
}
.btn-clear-all-tags:hover {
    text-decoration: underline;
}
.selected-tags-count {
    color: white;
    padding-top: 0.25rem;
    padding-left: 0.5rem;
    padding-bottom: 0.5rem;
}
.show-more-btn {
    background-color: #101113; /*   */
    color: #ffffff; /*   */
    padding: 4px 5px; /*   */
    border-radius: 8px; /*   */
    border: 1px solid #101113; /*     */
    font-size: 1.25rem;
}

.show-more-btn:hover {
    border: 1px solid #ffffff; /*     */
}
/*      */
.dropdown-tags {
    margin-top: 2.5rem;
    background-color: #131319;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); /*    */
    border-radius: 5px;
    padding: 5px;
    position: absolute; /*    */
    z-index: 10; /*       */
}
.dropdown-tags .dropdown-tag-item {
    display: flex; /*        */
    text-align: center; /*  ,   */
    align-items: center; /*  ,   */
    border: 1px solid #101113; /*   */
}
.dropdown-tags .dropdown-tag-item:hover {
    border: 1px solid white; /*     */
}
.dropdown-tags .dropdown-tag-item.active {
    border: 1px solid #007bff; /*     */
    background-color: #007bff;
}
.dropdown-tags .dropdown-tag-item.active:hover {
    border: 1px solid white; /*     */
}
.guide-tags-container {
    margin-top: 0.5rem; /*       */
    margin-bottom: 0;
    clear: both; /*    */
}
@media (max-width: 810px) {
    .tag-list{
        display: none;
    }
}